
import { originalUrl } from './urlList.js'

export const myRequest = (options) => {
	return new Promise((resolve,reject) => {
		uni.request({
			url:originalUrl+options.url,
			method:options.method || 'GET',
			data: options.data || {},
			success: (res) => {
				resolve(res)
			},
			fail: (err) => {
				uni.showToast({
					title:''
				})
				reject(err)
			}
		})
	})
}

export const maintenanceUrl = 'app/operation/'; //